import { Component, OnInit } from '@angular/core';
import { WebSocketService } from 'src/app/services/web-socket.service';
import { CmdsMain } from './../../models/ICmdsMain';

@Component({
  selector: 'app-tools',
  templateUrl: './tools.page.html',
  styleUrls: ['./tools.page.scss'],
})
export class ToolsPage implements OnInit {

  power: boolean = false;
  cmd_main_ui: CmdsMain = new CmdsMain();

  constructor(
    public webSocket: WebSocketService
  ) { }

  ngOnInit() {

  }

  powerChanged(){
    console.log(!this.power);
    if(!this.power){
      this.webSocket.sendWebSocket(this.cmd_main_ui.machineOn)
    } else {
      this.webSocket.sendWebSocket(this.cmd_main_ui.machineOff)
    }
  }

  getBackButtonText() {
    const win = window as any;
    const mode = win && win.Ionic && win.Ionic.mode;
    return mode === 'ios' ? 'Atrás' : 'Atrás';
  }

}
